	[GL.BLEND]: true,
	[GL.BLEND_SRC_RGB]: GL.ONE,
	[GL.BLEND_DST_RGB]: GL.ONE,
	[GL.BLEND_EQUATION_RGB]: GL.FUNC_ADD,
	[GL.BLEND_SRC_ALPHA]: GL.SRC_ALPHA,
	[GL.BLEND_DST_ALPHA]: GL.ZERO,
