const fetchURL = 'https://d35p9e4fm9h3wo.cloudfront.net/latestData.json'
const getItems = () => fetch(fetchURL).then(res => res.json())

const StreamChart = () => {
	const [items, setItems] = useState([])
	useEffect(() => {
		getItems().then(data => {
			const hist = data['historicalData']

			let keys = Object.entries(hist)
				.map(d => d[0])
				.reverse()
			let values = Object.entries(hist)
				.map(n => n[1].vaccines)
				.reverse()
			values = values.filter(obj => obj)
			let dose0 = values.map(v =>
				Object.entries(v).map(v => [v[0], v[1].total_administered])
			)
			// console.log(values.length)

			// console.log(values.length)
			let dose2 = values.map(v =>
				Object.entries(v).map(v => [v[0], v[1].immunized])
			)
			let dose1 = []
			for (let i = 0; i < values.length; i++) {
				let part = []
				for (let j = 0; j < dose0[i].length; j++) {
					/* console.log([dose0[i][j][0], dose0[i][j][1] - dose2[i][j][1]]) */
					part.push([dose0[i][j][0], dose0[i][j][1] - dose2[i][j][1]])
				}
				/* console.log(part) */
				dose1.push(part)
			}
			let data0 = []
			for (let i = 0; i < values.length; i++) {
				let part = {}
				for (let j = 0; j < dose0[i].length; j++) {
					part[dose0[i][j][0]] = dose0[i][j][1]
				}
				data0.push(part)
			}
			console.log(data0)
			setItems(data0)
		})
	}, [])
	return (
		<Container>
			<h4>Chart Title ({items.length})</h4>
			<Wrapper>
				{items && (
					<ResponsiveStream
						data={items}
						// animate={false}
						keys={['astra_zeneca', 'moderna', 'pfizer']}
						margin={{ top: 10, right: 15, bottom: 50, left: 40 }}
						axisTop={null}
						axisRight={null}
						axisBottom={{
							orient: 'bottom',
							tickSize: 5,
							tickPadding: 5,
							tickRotation: 0,
							legend: '',
							legendOffset: 36,
						}}
						axisLeft={{
							orient: 'left',
							tickSize: 5,
							tickPadding: 5,
							tickRotation: 0,
							legend: '',
							legendOffset: -40,
						}}
						offsetType='silhouette'
						colors={{ scheme: 'set1' }}
						fillOpacity={0.85}
						borderColor={{ theme: 'background' }}
						dotSize={8}
						dotColor={{ from: 'color' }}
						dotBorderWidth={2}
						dotBorderColor={{ from: 'color', modifiers: [['darker', 0.7]] }}
						legends={[
							{
								anchor: 'bottom',
								direction: 'row',
								justify: false,
								translateX: 0,
								translateY: 46,
								itemsSpacing: 0,
								itemDirection: 'left-to-right',
								itemWidth: 65,
								itemHeight: 10,
								itemOpacity: 0.75,
								symbolSize: 12,
								symbolShape: 'circle',
								symbolBorderColor: 'rgba(0, 0, 0, .5)',
								effects: [
									{
										on: 'hover',
										style: {
											itemBackground: 'rgba(0, 0, 0, .03)',
											itemOpacity: 1,
										},
									},
								],
							},
						]}
					/>
				)}
			</Wrapper>

			<p>Footer</p>
		</Container>
	)
}

export default StreamChart
